* {
    margin: 0%;
    padding: 0%;
}
.popup{
    position: absolute;
    height: 95vh;
    width: 95vw;
    border-radius: 50px;
    background-color: white;
}
.popup p{
    font-size: 1.5rem;
    font-weight: 700;
}
.card-details {
    display: flex;
    flex-wrap: wrap;
    background-color: white;
}
.top-right{
    width: 100%;
    display: flex;
    justify-content: flex-end;
    background-color: white;
}
.close-button{
    margin: 20px;
    border: none;
    background: none;
    height: 50px;
    width: 100px;
    border-radius: 50px;
    border: 2px solid black;
    background-color: black;
    color: white;
    font-size: 1.5rem;
    cursor: pointer;
}

.pchartcss,
.lchartcss {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 300px;
    height :300px;
    margin-left: 15%;
}
.tablecss{
    display: flex;
    justify-content: center;
    width: 100%;
    height: 100%;
    margin: auto;
    margin-top: 30px;
}
.heading h1{
    margin-left: 40%;
    margin-top: 20px;
    margin-bottom: 40px;
    text-decoration: underline;
}

.grid-container {
    display: grid;
    grid-template-columns: auto auto auto auto auto auto;
    grid-template-rows: auto auto auto auto;
    gap: 10px;
    margin: auto;
    height: 80vh;
    width: 80vw;
}

.grid-container>div {
    text-align: center;
    padding: 20px ;
}
text{
    size: 10px;
}
.grid-container>div {
    text-align: center;
    padding: 20px 0;
    font-size: 1rem;
    font-weight: 700;
    color: white;
}
.grid-container>div:hover{
    background-color:white!important;
    cursor: pointer;
}

.item1 {
    grid-row: 1 /span 3;
    grid-column: 1/span 1;
}

.item2 {
    grid-row: 4/span 4;
}

.item3 {
    grid-row: 1 /span 2;
    grid-column: 2 /span 1;
}

.item4 {
    grid-row: 3/ span 5;
    grid-column: 3 /span;
}

.item5 {
    grid-column: 3/span 5;
}

.item11 {
    grid-row: 2/ span 6;
}

.item12 {
    grid-row: 3/ span 5;
}

.item13 {
    grid-row: 4/ span 4;
}

.item8 {
    grid-row: 3/span 1;
    grid-column: 5 /span 1;
}
.dummy-data{
    margin-left:40%;
    margin-top: 20px;
    font-size: 1.5rem;
    font-weight: 700;
}
